}
.Avatar {
   width: 40px;
   height: 40px;
   background-repeat: no-repeat;
   background-position: center;
   background-size: cover;
   border-radius: 50%;
}

.Avatar-line {
   height: .25rem;
   margin-bottom: 0;
   background: tomato;
   border: none;
   -webkit-animation: line 500ms ease-in-out forwards;
           animation: line 500ms ease-in-out forwards;
}

.Avatar-line-hide {
   height: .25rem;
   margin-bottom: 0;
   border: none;
}

@-webkit-keyframes line {
   from {
      width: 0%;
   }
   to {
      width: 100%;
   }
}

@keyframes line {
   from {
      width: 0%;
   }
   to {
      width: 100%;
   }
}
