}

.ImageBackground-left {
   position: absolute;
   left: -12px;
   height: 100%;
   width: 30px;
   top: -13px;
   -webkit-filter: blur(5px);
           filter: blur(5px);
   background: #00255a;
}

.ImageBackground-left-aux {
   position: absolute;
   left: -12px;
   height: 103%;
   width: 30px;
   top: -13px;
   -webkit-filter: blur(5px);
   filter: blur(5px);
   background: #002b66;
}

.ImageBackground-right {
   position: absolute;
   right: -12px;
   height: 100%;
   width: 30px;
   top: -13px;
   -webkit-filter: blur(5px);
           filter: blur(5px);
