.Arrow-container {
   position: absolute;
   height: inherit;
   display: flex;
   align-items: center;
   z-index: 10;
}

.Arrow-to-right {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   color: white;
   text-decoration: none;
   font-family: 'Raleway', sans-serif;
   -webkit-animation: toRight 1s infinite ease-in-out alternate;
           animation: toRight 1s infinite ease-in-out alternate;
}

.Arrow-to-right img {
   height: 5vh;
   width: 5vh;
}

.Arrow-to-left {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   color: white;
   text-decoration: none;
   font-family: 'Raleway', sans-serif;
   -webkit-animation: toRight 1s infinite ease-in-out alternate;
           animation: toRight 1s infinite ease-in-out alternate;
}

.Arrow-to-left img {
   height: 5vh;
   width: 5vh;
   -webkit-transform: rotate(180deg);
           transform: rotate(180deg);
}

@-webkit-keyframes toRight {
   from {
      -webkit-transform: translateX(-10px);
              transform: translateX(-10px);
   }
   to {
      -webkit-transform: translateX(10px);
              transform: translateX(10px)
   }
}

@keyframes toRight {
   from {
      -webkit-transform: translateX(-10px);
              transform: translateX(-10px);
   }
   to {
      -webkit-transform: translateX(10px);
              transform: translateX(10px)
   }
}

@media only screen and (max-width: 500px) {
   .Arrow-container {
      align-items: flex-end;
   }
   .Arrow-to-right {
      margin-bottom: 10%;
      -webkit-transform: scale(0.8);
              transform: scale(0.8)
   }
   .Arrow-to-right span {
      width: 70%;
      text-align: center;
   }
   .Arrow-to-right img {
      height: 4vh;
      width: 4vh;
   }
