.Paragraph-container {
   position: absolute;
   color: white;
   font-family: 'Raleway', sans-serif;
   top: 30%;
   width: 50%;
   display: flex;
   flex-direction: column;
   left: 15%;
}

.Paragraph-hi {
   font-size: 3em;
}

.Paragraph-text-container {
   margin: 10px 5px;
}

.Paragraph-text {
   font-size: 1.6em;
   line-height: 1.5;
}

@media only screen and (max-width: 1000px) {
   .Paragraph-container {
      top: 20%;
   }
   .Paragraph-text {
      font-size: 1.3em;
   }
}

@media only screen and (max-width: 500px) {
   .Paragraph-container {
      top: 15%;
      width: 70%;
   }
   .Paragraph-text {
      font-size: 1.2em;
   }
}